import 'bootstrap'
import 'bootstrap/dist/css/bootstrap.css' // Import precompiled Bootstrap css
import '@fortawesome/fontawesome-free/css/all.css'

import $ from 'jquery';
import _ from 'lodash';
import {textToIndexDto} from '@ochuzor/todo.txt-parser';

import sampleTodos from './sample-todos';

function t(s,d){
    for(var p in d)
      s=s.replace(new RegExp('{'+p+'}','g'), d[p]);
    return s;
}

const uiState = {
    renderTodoList: true,
    renderTodoEdit: false,
};

$(function () {
    const todoTxtEdit = $('#todo-text-edit');
    const lstCntr = $('#oc-todo-lst-cntr');
    const compiled = _.template('<% _.forEach(todos, function(todo, i) { %><div todo-id="${ i+1 }" class="oc-todo-itm">${ todo }</div><% }); %>');
    
    function renderTodoList(lsTodos) {
        const todos = compiled({ todos: lsTodos });
        lstCntr.html(todos);
    }

    renderTodoList(sampleTodos);

    $('.oc-todo-itm').click(function() {
        const id = $(this).attr('todo-id');
        const todoTxt = $(this).text();
        // console.log(id, ':', todo)
        todoTxtEdit.val(todoTxt);
    });

    function onTodoTextChange() {
        const val = $(this).val();
        const data = _.pickBy(textToIndexDto(val), _.identity);
        console.log('value:', data)
    }

    todoTxtEdit.on('input', _.debounce(onTodoTextChange, 250, { 'maxWait': 1000 }))

    // var templateStr = $('#x-todo-list').html();
    // var template = _.template(templateStr);
    // var compiledTemplate = template({ name: "World" });
    // console.log(compiledTemplate)

    function render() {

    }
});